.top-section {
    display: flex;
    background-color: #263339;
    color: white;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
  }
  
  .top-section-left{
      display: flex;
      flex-direction: row;
      gap: 60px;
      margin-left: 5px;
      align-items: center;
      cursor: default;  
  }
  
  .left-content1{
      display: flex;
      gap: 15px;
      flex-direction: row;
      margin-left: 15px;

  }
  
  .status-box{
      display: flex;
      padding: 10px 15px;
      background-color: rgb(207, 57, 57);
      color: white;
      font-size: 17px;
      border-radius: 8px;
      transition: background-color 0.3s ease;
  }
  
  .status-box.On{
      background-color: rgb(10, 173, 10);
  }
  
  .left-content2{
      padding: 0px;
      display: flex;
      flex-direction: column;
      font-size: 17px;
      align-items: center;
  }
  
  .left-content2 span{
      font-family: 'Montserrat', sans-serif;
  }
  
  .left-content3{
      display: flex;
      flex-direction: row;
      gap: 10px;
      align-items: center;
      padding: 0px;
  }
  
  .left-content3 .item{
      display: flex;
      flex-direction: column;
      align-items: center;
      font-family: 'Montserrat', sans-serif;
  }
  
  .left-content3 button{
      background-color: #424c55;
      padding: 13px 13px;
      border-radius: 3px;
      border: none;
      cursor: pointer;
      height: 100%;
  }
  
  .top-section-right{
      display: flex;
      flex-direction: row;
      gap: 10px;
      margin-right: 10px;
      align-items: center;
  }
  
  .top-section-right button{
      background-color: #424c55;
      padding: 13px 13px;
      border-radius: 3px;
      border: none;
      cursor: pointer;
  }
  